li {
  float: right;
  position: relative;
  cursor: pointer;
  list-style-type: none;
  margin-left: 33px;
  margin-right: 33px;
  border: 2px solid salmon;
  transition: margin .1s, padding .1s, border 1s;
}

li:first-child {
  margin-left: 20px;
}

li:before {
  right: 100%;
}
li:after {
  left: 100%;
}

li:hover {
  margin-top: -10px;
  box-shadow: 0 4px 12px 0px burlywood;
}

li:hover:before, li:hover:after {
  height: 80%;
  background: cyan;
  transition: all .2s;
}

li:hover:after {
  transform: rotate( 360deg ) skew( 30deg ) translate( -2px, 9px );
}

/* -- CONTAINERS -- */

.Flex-container {
  /*text-align: center;*/
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-left: 10%;
  margin-right: 10%;
  background-color: #1f0047;
  color: chartreuse;
}

.Flex-container-left {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  margin-left: 10%;
  margin-right: 10%;
  background-color: #1f0047;
  color: chartreuse;
}

/* -- GAME STUFF -- */

#canvas {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

img[id="YYYYYY"] {
  cursor: pointer;
  border-radius: 10px;
  margin-top: 15px;
}

label {
  margin-right: 20px;
  font-size: large;
  justify-content: stretch;
}

/* -- TEXT -- */

p:hover {
  transform: scaleY( 1.25 )
}

#bigText {
  font-size: 40px;
  font-family: Impact;
  font-style: oblique;
  color: black;
  background-color: aqua;
}

#preText {
  font-size: 18px;
  padding: 20px;
}

#papyrus {
  font-weight: 900;
  opacity: 0.6;
}

/* * * -- INPUTS -- * * */

button:hover {
  transform: scale( 1.2 );
}

button:active {
  animation: spin 0.2s linear;
}

button {
  font-size: 14px;
  padding: 20px;
  width: 140px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-style: outset;
  border-width: 3px;
  border-radius: 10px;
  background-color: black;
  color: chartreuse;
  border-color: chartreuse;
}

button.YYY {
  border-color: #379000;
  color: #123dfb;
  background: #a2ff96;
  animation: pulse infinite 1s linear alternate;
}

button.more {
  color: #623afb;
  background: #f8cf66;
  animation: rainbow infinite 1s linear alternate-reverse;
}

button.tellMore {
  background-color: black;
  color: white;
}

/* -- MEDIA -- */

img[alt="logo"] {
  animation: spin infinite 20s linear alternate-reverse;
}

img[alt="cards"] {
  animation: awkward-slide 14s infinite alternate ease; 
}

/* -- MISC -- */

footer {
  margin-top: 20px;
  animation: slide infinite 15s ease-out;
}

header {
  text-align: center;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 11px;
}

#clickable {
  cursor: pointer;
}

.flashBG {
  animation: flash-bg 500ms linear infinite alternate;
  border-radius: 5%;
  align-content: stretch;
}

.flashFG {
  animation: flash-fg 200ms linear infinite alternate;
}

.fixed {
  position: fixed;
  align-self: flex-start;
  /*
  inline-size: 125vh;
  block-size: 50vw;
  */
} 
.hugCorners20 {
  animation: hug-corners 20s linear infinite;
} 
.hugCorners21 {
  animation: hug-corners 21s linear infinite;
} 
.hugCorners22 {
  animation: hug-corners 22s linear infinite;
} 
.hugCorners23 {
  animation: hug-corners 23s linear infinite;
}

.orbit {
  /*margin-left: 50%;
  margin-right: 50%;*/
  animation: orbit 20s linear infinite;
}

/* -- ANIMATIONS -- */

@keyframes rainbow {
  0% {
    background-color: red;
    color: green;
  }
  25% {
    background-color: yellow;
    color: red;
  }
  50% {
    background-color: blue;
    color: yellow;
  }
  100% {
    background-color: green;
    color: red;
  }
}

@keyframes pulse {
  from { transform: scale( 0.5 ); }
  to { transform: scale( 1.5 ); }
}

@keyframes orbit {
  from { transform: rotate( 0deg ) translateX( 250px ) rotate( 0deg ); }
  to { transform: rotate( 360deg ) translateX( 250px ) rotate( -360deg ); }
}

@keyframes hug-corners {
  0% { transform: translate( 0vw, 0vh ) }
  25% { transform: translate( 750vw, 0vh ) }
  50% { transform: translate( 750vw, 85vh ) }
  75% { transform: translate( 0vw, 85vh ) }
  100% { transform: translate( 0vw, 0vh ) }
}

@keyframes awkward-slide {
  0% { transform: translateX(-200px) }
  25% { transform: translateX(-100px) }
  50% { transform: translateX(100px) }
  75% { transform: translateX(-100px) }
  100% { transform: translateX(-200px) }
}

@keyframes flash-bw {
  from {
    color: white;
    background-color: black;
  }
  to {
    color: black;
    background-color: white;
  }
}

@keyframes flash-fg {
  from { color: black; }
  to { color: blue; }
}

@keyframes flash-bg {
  from { background-color: rgba(0, 255, 255, 0); }
  to { background-color: rgba(0, 255, 34, 0.466); }
}

@keyframes spin {
  from {
    transform: rotate( 0deg );
  }
  to {
    transform: rotate( 360deg );
  }
}
